<template>
  <el-menu
    :default-active="$route.path"
    :router="true"
    class="el-menu-demo"
    mode="horizontal"
    background-color="#324057"
    text-color="#fff"
    active-text-color="#E6A23C">
    <el-menu-item index="/index">
      文件总揽
    </el-menu-item>
    <el-menu-item index="/fileDetail">
      文件详情
    </el-menu-item>
    <el-submenu index="3">
      <template slot="title">用户管理</template>
      <el-menu-item index="/user" v-if="superShow=='true'">用户列表</el-menu-item>
      <el-menu-item  index="/login" @click.native="_logout">退出登陆</el-menu-item>
    </el-submenu>
  </el-menu>
</template>

<script>
  import {logout} from 'api/user'
  export default {
    name: 'tabs',
    data () {
      return {
        superShow: localStorage.getItem('super') || false
      }
    },
    methods: {
      _logout () {
        logout().then(res => {
          console.log(res)
        })
      }
    }
  }
</script>

<style scoped lang="stylus">
  .el-menu-demo /deep/ .el-submenu__title i
    color #fff
  /*.el-menu-demo /deep/ .el-menu--horizontal>.el-menu-item*/
    /*font-weight bold*/
  /*.el-menu-demo /deep/ .el-menu--horizontal>.el-menu-item.is-active*/
    /*font-weight bold*/
</style>



