<template>
  <div class="TextScroll">
    <transition   name="scroll-left">
      <div v-if="isShow">{{dataString}}</div>
    </transition>
  </div>

</template>

<script>
  export default {
    name: 'textScroll',
    data () {
      return {
        isShow: false, // 当前索引  当v-for中的index等于count时 v-show=true 即显示当前元素
        intervalId: null, // 定时器ID
        playTime: 3000 // 定时器执行间隔
      }
    },
    props: {
      dataString: {
        type: String,
        default: function () {
          return '暂时没有消息'
        }
      }
    },
    created () {
      this.intervalId = setInterval(() => { // 定义定时器
        this.isShow = !this.isShow
      }, this.playTime)
    },
    destroyed () {
      clearInterval(this.intervalId) // 清除定时器
    }
  }
</script>

<style scoped lang="stylus">
  /* 向左滚动动画 */
  .scroll-left-enter-active,
  .scroll-left-leave-active
    transition: all 5s linear
  .scroll-left-enter
    transform: translate3d(100%,0,0)
  .scroll-left-leave-to
    transform: translate3d(-100%,0,0)
  .TextScroll
    width 95%
    overflow hidden
</style>



